import { useState } from "react";

export default function SearchBar({ onGenerate }) {
  const [input, setInput] = useState("");
  return (
    <div className="mb-8">
      <h1 className="text-3xl font-bold text-gray-800 mb-4 text-center">
        Generate Delicious Recipes with AI
      </h1>
      <p className="text-gray-600 text-center mb-6">
        Enter your ingredients and let AI create amazing recipes for you!
      </p>
      <form
        onSubmit={(e) => {
          e.preventDefault();
          onGenerate(input);
          setInput("");
        }}
        className="flex max-w-2xl mx-auto shadow-lg rounded-lg overflow-hidden"
      >
        <input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="e.g. chicken, basil, garlic, tomatoes..."
          className="flex-grow p-4 text-lg focus:outline-none focus:ring-2 focus:ring-green-500"
        />
        <button
          type="submit"
          className="bg-gradient-to-r from-green-500 to-green-600 px-8 py-4 text-white font-semibold hover:from-green-600 hover:to-green-700 transition-all duration-200"
        >
           Generate Recipe
        </button>
      </form>
    </div>
  );
}
